*, *::after, *::before {
    box-sizing: border-box
}

a {
    color: #dd85ac;
    text-decoration: none
}

img {
    max-width: 100%
}

input {
    font-family: open sans, sans-serif;
    font-size: 1.6rem
}

input[type=search]::-webkit-search-decoration, input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-results-button, input[type=search]::-webkit-search-results-decoration {
    display: none
}

select, input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    border: none
}

input[type=search] {
    border-radius: 0;
    margin-top: 0
}

select {
    background-color: transparent;
    cursor: pointer
}

.cd-select::after {
    content: '';
    position: absolute;
    z-index: 1;
    right: 4px;
    top: 50%;
    -webkit-transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -ms-transform: translateY(-50%);
    -o-transform: translateY(-50%);
    transform: translateY(-50%);
    width: 16px;
    height: 16px;
    background: url(../img/cd-icons.svg) no-repeat 0 0;
    pointer-events: none
}

.cd-text-replace {
    color: transparent;
    text-indent: 100%;
    overflow: hidden;
    white-space: nowrap
}

.cd-main-header {
    position: relative;
    z-index: 4;
    height: 64px;
    background-color: #090a0b;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-transition: -webkit-transform .3s;
    -moz-transition: -moz-transform .3s;
    transition: transform .3s
}

.cd-main-header.nav-is-visible {
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    transform: translateX(0)
}

.cd-main-header::before {
    display: none;
    content: 'mobile'
}

@media only screen and (min-width: 1024px) {

    .cd-main-header.nav-is-visible {
        -webkit-transform: translateX(0);
        -moz-transform: translateX(0);
        -ms-transform: translateX(0);
        -o-transform: translateX(0);
        transform: translateX(0)
    }

    .cd-main-header::before {
        content: 'desktop'
    }
}

.cd-main-nav-wrapper {
    padding: 0 5%
}

.cd-main-header .site-nav{
    position: static;
}

.cd-main-header .site-nav .site-nav-logo {
    z-index: 3000;
}

@media only screen and (max-width: 700px) {
    .cd-main-header .site-nav .site-nav-content{
        display: none;
    }
}



.cd-main-nav-wrapper::after {
    clear: both;
    content: "";
    display: table
}

.cd-logo {
    display: inline-block;
    height: 100%;
    margin-left: 15px;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden
}

.cd-logo img {
    display: block;
    margin-top: 14px
}

@media only screen and (min-width: 1024px) {
    .cd-logo {
        position: absolute;
        z-index: 3;
        margin-left: 5%
    }

    .cd-logo img {
        margin-top: 29px
    }
}

.cd-nav-trigger {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 50px;
    background: #090a0b;
    z-index: 3000;
}

.cd-nav-trigger span {
    position: absolute;
    height: 2px;
    width: 20px;
    left: 50%;
    top: 50%;
    bottom: auto;
    right: auto;
    -webkit-transform: translateX(-50%) translateY(-50%);
    -moz-transform: translateX(-50%) translateY(-50%);
    -ms-transform: translateX(-50%) translateY(-50%);
    -o-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
    background-color: #fff;
    -webkit-transition: background-color .3s;
    -moz-transition: background-color .3s;
    transition: background-color .3s
}

.cd-nav-trigger span::before, .cd-nav-trigger span::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transition: -webkit-transform .3s;
    -moz-transition: -moz-transform .3s;
    transition: transform .3s
}

.cd-nav-trigger span::before {
    -webkit-transform: translateY(-6px);
    -moz-transform: translateY(-6px);
    -ms-transform: translateY(-6px);
    -o-transform: translateY(-6px);
    transform: translateY(-6px)
}

.cd-nav-trigger span::after {
    -webkit-transform: translateY(6px);
    -moz-transform: translateY(6px);
    -ms-transform: translateY(6px);
    -o-transform: translateY(6px);
    transform: translateY(6px)
}

.nav-is-visible .cd-nav-trigger span {
    background-color: transparent
}

.nav-is-visible .cd-nav-trigger span::before {
    -webkit-transform: translateY(0) rotate(-45deg);
    -moz-transform: translateY(0) rotate(-45deg);
    -ms-transform: translateY(0) rotate(-45deg);
    -o-transform: translateY(0) rotate(-45deg);
    transform: translateY(0) rotate(-45deg)
}

.nav-is-visible .cd-nav-trigger span::after {
    -webkit-transform: translateY(0) rotate(45deg);
    -moz-transform: translateY(0) rotate(45deg);
    -ms-transform: translateY(0) rotate(45deg);
    -o-transform: translateY(0) rotate(45deg);
    transform: translateY(0) rotate(45deg)
}

@media only screen and (min-width: 1024px) {
    .cd-nav-trigger {
        display: none
    }
}

.cd-main-nav {
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    width: 260px;
    height: 100vh;
    overflow: auto;
    background: #1f2029;
    visibility: hidden;
    -webkit-transition: visibility 0s .3s;
    -moz-transition: visibility 0s .3s;
    transition: visibility 0s .3s
}

.cd-main-nav a, .cd-main-nav input[type=search] {
    display: block;
    font-size: 1.6rem;
    padding: 0 2em 0 1em;
    line-height: 50px;
    color: #fff;
    border-bottom: 1px solid #333545;
    -webkit-transition: -webkit-transform .3s, opacity .3s;
    -moz-transition: -moz-transform .3s, opacity .3s;
    transition: transform .3s, opacity .3s
}

.cd-main-nav input[type=search] {
    background-color: #292a37;
    -webkit-transition: background-color .3s;
    -moz-transition: background-color .3s;
    transition: background-color .3s
}

.cd-main-nav input[type=search]:focus {
    background-color: #333545
}

.cd-main-nav.nav-is-visible {
    -webkit-transition: visibility 0s;
    -moz-transition: visibility 0s;
    transition: visibility 0s;
    visibility: visible;
    -webkit-overflow-scrolling: touch
}

@media only screen and (min-width: 1024px) {
    .cd-main-nav {
        position: static;
        visibility: visible;
        width: auto;
        height: 80px;
        background: 0 0;
        overflow: visible;
        float: right
    }

    .cd-main-nav li {
        display: inline-block;
        float: left;
        height: 100%;
        padding: 1.375em .4em
    }

    .cd-main-nav a {
        padding: .625em .8em;
        line-height: 1;
        border-bottom: none
    }

    .cd-main-nav a:hover {
        opacity: .6
    }
}

.cd-main-search .cd-select {
    display: none
}

.cd-main-search .close {
    display: none
}

.cd-main-search input[type=search] {
    width: 100%;
    color: #fff
}

.cd-main-search input[type=search]:focus {
    outline: none
}

.cd-main-search input[type=search]::-webkit-input-placeholder {
    color: #5c5d6a
}

.cd-main-search input[type=search]::-moz-placeholder {
    color: #5c5d6a
}

.cd-main-search input[type=search]:-moz-placeholder {
    color: #5c5d6a
}

.cd-main-search input[type=search]:-ms-input-placeholder {
    color: #5c5d6a
}

.cd-main-search form{
    display: none;
}
@media only screen and (min-width: 1024px) {
    .cd-main-search {
        position: absolute;
        z-index: 2000;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: #14151c;
        opacity: 0;
        visibility: hidden;
        -webkit-transition: opacity .3s, visibility .3s;
        -moz-transition: opacity .3s, visibility .3s;
        transition: opacity .3s, visibility .3s
    }

    .cd-main-search form {
        position: relative;
        height: 100%;
        margin: 0 95px;
    }

    .cd-main-search input[type=search] {
        box-sizing: border-box;
        font-size: 2.4rem;
        height: 100%;
        background-color: transparent;
        padding-right: 60px
    }

    .cd-main-search .cd-select {
        display: inline-block;
        position: absolute;
        right: 0;
        top: 50%;
        bottom: auto;
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -o-transform: translateY(-50%);
        transform: translateY(-50%);
        color: #5c5d6a;
        font-size: 1.4rem;
        overflow: hidden
    }

    .cd-main-search span {
        display: inline-block
    }

    .cd-main-search option {
        color: #333545;
        background-color: #fff
    }

    .cd-main-search select {
        position: absolute;
        right: 0;
        top: 50%;
        bottom: auto;
        -webkit-transform: translateY(-50%);
        -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        -o-transform: translateY(-50%);
        transform: translateY(-50%);
        opacity: 0;
        color: transparent
    }

    .cd-main-search select:focus {
        outline: none
    }

    .cd-main-search select:focus + span.selected-value {
        background-color: #3d3f4e
    }

    .cd-main-search .selected-value {
        color: #fff;
        pointer-events: none;
        -webkit-transition: background-color .3s;
        -moz-transition: background-color .3s;
        transition: background-color .3s
    }

    .cd-main-search select, .cd-main-search .selected-value {
        padding: .5em 1.7em .5em .3em;
        font-size: 1.4rem;
        border-radius: 3px
    }

    .no-pointerevents .cd-main-search .selected-value {
        display: none
    }

    .no-pointerevents .cd-main-search select {
        position: relative;
        color: #fff;
        -webkit-transform: translateY(0);
        -moz-transform: translateY(0);
        -ms-transform: translateY(0);
        -o-transform: translateY(0);
        transform: translateY(0)
    }

    .no-pointerevents .cd-main-search select:focus {
        background-color: #3d3f4e
    }

    .cd-main-search .close {
        display: inline-block;
        position: absolute;
        right: 1%;
        top: 50%;
        height: 40px;
        width: 40px;
        -webkit-transform: translateY(-50%) scale(0);
        -moz-transform: translateY(-50%) scale(0);
        -ms-transform: translateY(-50%) scale(0);
        -o-transform: translateY(-50%) scale(0);
        transform: translateY(-50%) scale(0);
        -webkit-transition: -webkit-transform .3s;
        -moz-transition: -moz-transform .3s;
        transition: transform .3s
    }

    .cd-main-search .close::before, .cd-main-search .close::after {
        content: '';
        position: absolute;
        height: 20px;
        width: 2px;
        background-color: #fff;
        left: 50%;
        top: 50%
    }

    .cd-main-search .close::before {
        -webkit-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
        -moz-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
        -ms-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
        -o-transform: translateX(-50%) translateY(-50%) rotate(-45deg);
        transform: translateX(-50%) translateY(-50%) rotate(-45deg)
    }

    .cd-main-search .close::after {
        -webkit-transform: translateX(-50%) translateY(-50%) rotate(45deg);
        -moz-transform: translateX(-50%) translateY(-50%) rotate(45deg);
        -ms-transform: translateX(-50%) translateY(-50%) rotate(45deg);
        -o-transform: translateX(-50%) translateY(-50%) rotate(45deg);
        transform: translateX(-50%) translateY(-50%) rotate(45deg)
    }

    .cd-main-search.is-visible {
        opacity: 1;
        visibility: visible
    }

    .cd-main-search.is-visible .close {
        -webkit-transform: translateY(-50%) scale(1);
        -moz-transform: translateY(-50%) scale(1);
        -ms-transform: translateY(-50%) scale(1);
        -o-transform: translateY(-50%) scale(1);
        transform: translateY(-50%) scale(1)
    }

    .animate-search .cd-main-search.is-visible {
        -webkit-animation: cd-slide-in .3s;
        -moz-animation: cd-slide-in .3s;
        animation: cd-slide-in .3s
    }
}

@-webkit-keyframes cd-slide-in {
    0% {
        -webkit-transform: translateY(-100%)
    }
    100% {
        -webkit-transform: translateY(0)
    }
}

@-moz-keyframes cd-slide-in {
    0% {
        -moz-transform: translateY(-100%)
    }
    100% {
        -moz-transform: translateY(0)
    }
}

@keyframes cd-slide-in {
    0% {
        -webkit-transform: translateY(-100%);
        -moz-transform: translateY(-100%);
        -ms-transform: translateY(-100%);
        -o-transform: translateY(-100%);
        transform: translateY(-100%)
    }
    100% {
        -webkit-transform: translateY(0);
        -moz-transform: translateY(0);
        -ms-transform: translateY(0);
        -o-transform: translateY(0);
        transform: translateY(0)
    }
}

